





























import Vue from 'vue'
import Component from 'vue-class-component'
import { Watch } from 'vue-property-decorator'
import { TodoConfig } from '@/store/modules/todos/types'
import Todo from './Todo.vue'
import db from '@/main'
import { mapActions, mapState } from 'vuex'

const filters = (selected: string) => (todos: TodoConfig[]) => {
  return todos.filter(todo => {
    switch (selected) {
      case ('all'): return todo
      case ('active'): return !todo.completed
      case ('completed'): return todo.completed
      default: return todo
    }
  })
}

@Component({
  components: {
    Todo
  },
  computed: {
    ...mapState('todos', [
      'todos',
      'newTodo'
    ])
  },
  methods: {
    ...mapActions('todos', [
      'clearCompleted',
      'completeAll',
      'applyFilter',
      'setNewTodo',
      'clearNewTodo',
      'loadTodos'
    ])
  }
})

export default class TodoList extends Vue {
  public todos!: TodoConfig[]
  public filters!: object

  selectedFilter = 'all';
  created () {
    this.$store.dispatch('todos/loadTodos', db.collection('todos'))
  }
  addTodoItem () {
    this.$store.dispatch('todos/addTodo')
    this.$store.dispatch('todos/clearNewTodo')
  }
  @Watch('$route', { immediate: true, deep: true })
  onUrlChange (to: any) {
    this.selectedFilter = to.params.filter
  }

  get filteredTodos () { return filters(this.selectedFilter)(this.todos) }
}
